<template>
    <div>
        <topNavBar />
        <leftSideBar />
        <div class="content">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-md-12">
                        <h2 style="display:inline;">{{activeQuestion+1}}/{{exam.length}}</h2>
                        <button class="btn btn-outline-default btn-sm pull-right" @click="submitExam">Submit</button>
                    </div>

                    <div class="col-md-12">

                        <b-form-group >
                            
                            <ol style="list-style: none;min-height: 300px;" v-if="exam.length >= 1" :set="choices = JSON.parse(exam[activeQuestion].choices)" >
                                <div>
                                    <label>{{exam[activeQuestion].title}} </label>
                                </div>
                                <div   v-for="(choice, indexQ) in choices" style="font-weight:bold;font-size:13px;color:black;opacity:0.9;" :key="indexQ">
                                    
                                    <div style="padding-left:1px;margin-bottom:1px;" class="choiceSelect">
                                        <b-form-radio 
                                            v-model="answer"
                                            name="some-radios" 
                                            :value= "choice.id"
                                            style="padding-top:4px;"
                                            @change="checkedRadio(choice.id, activeQuestion)"
                                        >
                                        
                                        </b-form-radio>
                                        <li style="padding-left:10px;margin-left:20px;padding-top:5px;padding-bottom:5px;width:95% !important;margin-top:-25px;margin-bottom: 5px;" class="choiceOption">
                                            <span v-if="choice.answer.indexOf('<img') == -1"> <span>{{ indexQ+1 }})</span>  {{ choice.answer}} </span>
                                            <span v-if="choice.answer.indexOf('<img') !== -1"><span>{{ indexQ+1 }})</span> <span v-html="choice.answer"></span></span>
                                        </li>
                                    </div>
                                </div>
                            </ol>
                            <button class="btn btn-md  btn-default pull-left" @click="prevQuestion" :disabled="(activeQuestion) <= 0">Previous</button>
                            <button class="btn btn-md  btn-primary pull-right" @click="nextQuestion" :disabled="(activeQuestion+1) >= exam.length">Next</button>
                        </b-form-group>
                        
                    </div>
                    
                </div>
                
            </div>
        </div>
        <div class="watch" :class="{ 'alertTime': leftTime.minutes >=20  }">
            <h4>{{leftTime.minutes}}:{{leftTime.seconds}} Min</h4>
        </div>
    </div>
</template>
<script>
import topNavBar from '../includes/topNavBar';
import leftSideBar from '../includes/leftSideBar';
import axios from 'axios';
export default {
    name:'student',
    components:{
        topNavBar,
        leftSideBar,
    },
    data(){
        return {
            exam : [],
            activeQuestion : 0,
            student: this.$store.state.activeUser,
            answer : null,
            allAnswers: [],
            exam_starting_time: new Date().getTime(),
            interval: null,
            intervals:{
                second: 1000,
                minute: 1000 * 60,
                hour: 1000 * 60 * 60,
                day: 1000 * 60 * 60 * 24
            },
            leftTime : {
                days:0,
                hours:0,
                minutes:0,
                seconds:0,
            },
        }
    },
    destroyed() {
        clearInterval(this.interval);    
    },
    created(){
        this.retriveExam()
    },
    methods:{
        updateDiffs() {
            let diff = Math.abs(Date.now() - this.exam_starting_time.getTime());
            this.leftTime.days = Math.floor(diff / this.intervals.day);
            diff -= this.leftTime.days * this.intervals.day;
            this.leftTime.hours = Math.floor(diff / this.intervals.hour);
            diff -= this.leftTime.hours * this.intervals.hour;
            this.leftTime.minutes = Math.floor(diff / this.intervals.minute);
            diff -= this.leftTime.minutes * this.intervals.minute;
            this.leftTime.seconds = Math.floor(diff / this.intervals.second);
        },
        submitExam: function(){
            let results = {};
            let exam_id = this.$route.params.exam_id;
            let student_id = this.student.id;
            this.allAnswers.forEach((answer,index) => {
                let question_id = this.exam[index].id;
                results[question_id] = answer;
            });
            results = JSON.stringify(results);
            let url = `${this.$store.state.serverAddress}/student/${student_id}/exams/${exam_id}/workplace/submit`;

            this.$snotify.async('Please while we examine the exam', 'Uploading', () => new Promise((resolve, reject) => {
                axios({
                    url: url,
                    data: {
                        studentAnswers : results,
                    },
                    method:'POST',
                    headers:{
                        Authorization: this.$store.state.token,
                        "Content-type": "application/json"
                    }
                })
                .then(response => {
                    if(response.data.status == 200){
                        setTimeout(()=>{
                            this.$router.push({name: 'dashboard.student.exam.review', params:{'exam_id': exam_id}});
                        }, 2000)
                        resolve({
                            title: 'Success',
                            body: response.data.message,
                            timeout: 2000,
                            config: {
                                closeOnClick: true,
                                timeout: 2000,
                            }
                        });

                        
                    }else{
                       reject({
                        title: 'Error!!!',
                        body: response.data.message,
                        timeout: 2000,
                        config: {
                            closeOnClick: true,
                            timeout: 2000,
                        }
                    }); 
                    }
                    
                }).catch(error =>{
                    reject({
                        title: 'Error!!',
                        body: "Sorry there was some error, try agin later!!",
                        timeout: 2000,
                        config: {
                            closeOnClick: true,
                            timeout: 2000,
                        }
                    });
                });
            }));
        },
        checkedRadio: function(choice_id, question_num){
            this.allAnswers[question_num] = choice_id;
        },
        prevQuestion: function(){
            if(this.activeQuestion-1 >=0){
                this.activeQuestion--;
                this.answer = false;
            }
            if(this.allAnswers[this.activeQuestion] !== undefined && this.allAnswers[this.activeQuestion] !== null){
                this.answer = this.allAnswers[this.activeQuestion];
            }
            
        },
        nextQuestion: function(){
            if(this.activeQuestion+1 <= this.exam.length-1){
                this.activeQuestion++;
                this.answer = false;
            }
            if(this.allAnswers[this.activeQuestion] !== undefined && this.allAnswers[this.activeQuestion] !== null){
                this.answer = this.allAnswers[this.activeQuestion];
            }
            
        },
        shuffleArray: function(array) {
            for (var i = array.length - 1; i > 0; i--) {
                var j = Math.floor(Math.random() * (i + 1));
                var temp = array[i];
                array[i] = array[j];
                array[j] = temp;
            }
            return array;
        },
        retriveExam: function(){
            let student_id = this.student.id;
            let exam_id = this.$route.params.exam_id;
            axios({
                url: this.$store.state.serverAddress +`/student/${student_id}/exams/${exam_id}/workplace`,
                method: 'GET',
                data : null,
                headers:{
                    Authorization : this.$store.state.token,
                }
            }).then((result)=>{
                if(
                    result.data.status !== 200 
                    &&
                    result.data.message.indexOf("You have done this exam already") !== -1){
                    
                    this.$router.push({name: 'dashboard.student.exam.review', params:{'exam_id': exam_id}});
                }else{
                    this.exam = result.data.exam;
                    this.exam_starting_time = new Date(result.data['starting_time']);
                    this.interval = setInterval(() => {
                        this.updateDiffs();
                        if(this.leftTime.minutes >= 20){
                            clearInterval(this.interval);
                            this.submitExam();
                        }
                    },1000);
                    this.updateDiffs();
                    if(this.leftTime.minutes >= 20){
                        this.leftTime.minutes = 20;
                        this.leftTime.seconds = '00';
                        clearInterval(this.interval);
                        this.submitExam();

                    }
                    
                }
                
            }).catch(error=>{
                console.log(error);
            })
        }
    }
    
}
</script>
<style lang="scss">
    body{
        background: #FAFAFA !important;
    }
    li{
        span{
            span{
                img{
                    height: 50px !important;
                    width: 50px !important;
                }
            }
        }
    }
</style>
<style scoped lang='scss'>
    
    .content{
        top: 100px;
        background: white;
        position: absolute;
        left: 100px;
        box-shadow: 0px 7px 5px 0px rgba(0, 0, 0, 0.06);
        margin-bottom: 50px;
        width: calc(100% - 150px);

    }
    h4{
        font-weight: bold;
        font-size: 17px;
        color: rgb(129, 5, 216);
    }


    table{
        padding-top: 50px;
        border: none;
        thead{
            border: none;
            th{
                border: none;
                font-size: 12px;
                opacity: 0.5;
                padding: 5px;
            }
        }

        tbody{
            border: none;
            tr:nth-child(even), tr:hover{
                background-color: rgba(211, 211, 211, 0.123);
            }
            
            tr{
                border: none;
                border-top: 1px solid lightgray;
                
                td{
                    border: none;
                    font-size: 13px;
                    
                    padding: 5px;

                    i{
                        font-size: 16px;
                    }

                    span{
                        font-size: 12px;
                    }
                }
            }
        }
    }

    .watch{
        position: fixed;
        bottom: 0;
        left: 50px;
        width: 100px;
        height: 60px;
        background-color:rgb(129, 5, 216);
        
        display: flex;
        justify-content: center;
        align-items: center;

        h4{
            color: white;
            margin: 0;
        }
    }

    .alertTime{
        animation: alerting 1s linear 0.1s infinite backwards  ;
        transition: all .4s;
    }
    @keyframes alerting {
        100%{ background-color: rgba(255, 0, 0, 0.705)}
    }

    

</style>